<template>
  <section class="modal">
    <div class="decoration-box modal-layer-helper">
    </div>
    <div class="dummy-layer modal-layer-helper">
      <div class="container flowers-container">
        <div class="flowers-gabarit">
          <img class="flower flower-black" src="/static/flowers/black.png">
          <img class="flower flower-lady" src="/static/flowers/lady.png">
        </div>
      </div>
      <!-- <my-location :motion="motion"></my-location> -->
    </div>
    <div class="container">
      <div class="table">
        <div class="table-cell">
          <div class="animtion-helper">
            <a class="outbound-cta" href="https://github.com/trinketmage" title="github trinketmage" target="_blank">github.com/trinketmage</a>
            <a class="outbound-cta" href="https://codepen.io/trinketmage/pens/popular/" title="codepen trinketmage" target="_blank">codepen.io/trinketmage</a>
            <a class="outbound-cta" href="mailto:tranremi@live.fr" title="email tran rémi">tranremi@live.fr</a>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import {TweenMax} from 'gsap'
// import MyLocation from './Location.vue'
export default {
  props: ['motion'],
  components: {
    // MyLocation
  },
  mounted () {
    TweenMax.set(this.$el.querySelector('.flowers-container'), { perspective: 800 })
    var $bg = this.$el.querySelectorAll('.flower')
    function findNewPoint (point, angle, distance) {
      var result = {}
      result.x = Math.round(
        Math.cos(angle * Math.PI / 180) * distance + point.x
      )
      result.y = Math.round(
        Math.sin(angle * Math.PI / 180) * distance + point.y
      )
      return result
    }
    var handleMove = event => {
      console.log(event)
      var p1 = {
        x: event.clientX,
        y: event.clientY
      }
      var p2 = {
        x: window.innerWidth / 2,
        y: window.innerHeight / 2
      }
      var p3y = p2.y - p1.y
      var p3x = p2.x - p1.x
      // var angleRadians = Math.atan2(p3y, p3x)
      var angleDeg = Math.atan2(p2.y - p1.y, p2.x - p1.x) * 180 / Math.PI

      var distance = Math.sqrt(p3y * p3y + p3x * p3x)
      distance = distance / window.innerWidth / 2 * 100
      var point = findNewPoint({ x: 0, y: 0 }, angleDeg, distance)
      console.log(point.y)
      TweenMax.killTweensOf($bg)
      TweenMax.to($bg[0], 0.3, {
        force3D: true,
        x: point.x * 1,
        y: point.y * 1,
        rotationX: -point.y * 0.5,
        rotationY: point.x * 0.5,
        transformOrigin: '50% 100%'
      })
      TweenMax.to($bg[1], 0.3, {
        force3D: true,
        x: point.x * 1.1,
        y: point.y * 1.05,
        rotationX: -point.y * 0.75,
        rotationY: point.x * 0.9,
        transformOrigin: '50% 100%'
      })
    }

    this.$el.addEventListener('mousemove', handleMove, false)
  }
}
</script>

<style lang="scss" scoped>
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  .decoration-box {
    // background-color: rgba(128,255,229,.25);
    // background-color: rgba(128, 255, 229, 1);
    background-color: #f9e1d5;
    // background-color: #ceb888;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
  .dummy-layer {
    overflow: hidden;
    // background-color: rgba(0,0,0,.75);
    // background-color: #1c1c1c;
    // background-color: #252537;
    background-color: #ffffff;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
  .container {
    pointer-events: none;
    height: 100%;
    position: relative;
    z-index: 1;
  }
  .table {
    height: 100%;
    display: table;
  }
  .table-cell {
    display: table-cell;
    vertical-align: middle;
  }
  a {
    pointer-events: all;
    display: block;
    line-height: 2;
    color: #717171;
    transition: color 0.3s ease;
    &:hover {
      color: #f9e1d5;
    }
  }
  .flowers-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    box-sizing: border-box;
    .flower-black {
      width: 80px;
      margin-top: -16px;
    }
    .flower-lady {
      width: 120px;
    }
  }
}
</style>



