<template>
  <div id="app">
    <mast-head></mast-head>
    <head-intro v-if="cover" :progress="progress.value"></head-intro>
    <about-me></about-me>
    <skills></skills>
    <showcase></showcase>
    <!-- <interests></interests> -->
    <sketches></sketches>
    <nav-bar></nav-bar>

    <transition
        v-on:enter="enterModal"
        v-on:leave="leaveModal"
        v-bind:css="false"
        mode="out-in"
        >
        <keep-alive>
          <modal v-if="modalState"></modal>
        </keep-alive>
    </transition>
  </div>
</template>

<script>
import Doodle from 'doodle-svg'
import {TweenMax, TimelineMax, Quint, Sine, Linear} from 'gsap'
import 'gsap/RoundPropsPlugin'
import ScrollMagic from 'scrollmagic'
// import 'imports-loader?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/debug.addIndicators'
import 'imports-loader?define=>false!scrollmagic/scrollmagic/uncompressed/plugins/animation.gsap'

import HeadIntro from './pure/HeadIntro.vue'
import MastHead from './pure/MastHead.vue'
import AboutMe from './pure/AboutMe.vue'
import Skills from './pure/Skills.vue'
import Showcase from './pure/Showcase.vue'
// import Interests from './pure/Interests.vue'
import Sketches from './pure/Sketches/index.vue'
// import MyLocation from './pure/Location.vue'
import NavBar from './pure/NavBar.vue'
import Modal from './pure/Modal.vue'

export default {
  components: {
    HeadIntro,
    MastHead,
    AboutMe,
    Skills,
    Showcase,
    // Interests,
    Sketches,
    // MyLocation,
    NavBar,
    Modal
  },
  data () {
    return {
      cover: true,
      progress: {
        value: 0
      },
      modalState: false,
      controller: new ScrollMagic.Controller()
    }
  },
  mounted () {
    this.appReady()
    var doodle = new Doodle()
    var list = this.$el.querySelectorAll('.head-intro .main-logo path')
    this.doodle = doodle.normalizeGroup(list)
    var tl = new TimelineMax({onComplete: () => {
      this.cover = false
    }})
    tl
    .staggerTo(this.doodle, 3, {end: 1, ease: Quint.easeInOut}, 0.15, 'b')
    .to(this.progress, 3 + list.length * 0.15, {value: 100, roundProps: 'value'}, 'b')
    .to(this.$el.querySelector('.head-intro'), 1.5, {y: '-100%', ease: Quint.easeInOut}, 'd')
    .to(this.$el.querySelector('.head-intro .container'), 1.5, {y: '100%', ease: Quint.easeInOut}, 'd')
    .staggerFrom(this.$el.querySelectorAll('.nav-bar .thumb'), 0.3, {x: '100%', ease: Sine.easeOut, clearProps: 'all'}, 0.15, 'c')
    .staggerFrom(this.$el.querySelectorAll('.menu-helper a'), 0.3, {alpha: 0, x: -10, ease: Sine.easeOut, clearProps: 'all'}, 0.15, 'c')
    // .set(this.$el.querySelector('.head-intro'), {display: 'none'})
  },
  methods: {
    appReady () {
      this.$nextTick(() => this.setScroll())
    },
    setScroll () {
      var doodle = new Doodle()
      var list = this.$el.querySelectorAll('.mast-head .main-logo path')
      this.doodle = doodle.normalizeGroup(list)
      const tl = new TimelineMax()
      TweenMax.set(this.$el.querySelectorAll('.mast-head, .about-me'), {backgroundColor: 'rgba(255,255,255,0)'})
      tl
        .set(this.$el.querySelectorAll('.mast-head .main-logo path'), {alpha: 1})
        .fromTo(this.doodle, 2, {origin: 0, end: 1}, {origin: 0.5, end: 0.5})
        .set(this.$el.querySelectorAll('.mast-head .main-logo path'), {alpha: 0})
        // .fromTo(document.body, 0.5, {backgroundColor: 'rgb(255,255,255)'}, {backgroundColor: 'rgb(34, 34, 34)'})

      var tl2 = new TimelineMax({ paused: true })
      tl2
        .staggerTo(this.$el.querySelectorAll('.baseline, .navigation-helper'), 0.3, {alpha: 0}, 0.15)
      new ScrollMagic.Scene({duration: 300, offset: 1})
        // .addIndicators()
        .setTween(tl)
        .addTo(this.controller)
        .on('start', e => {
          e.state === 'BEFORE' ? tl2.tweenTo(0) : tl2.play()
        })

      const projects = this.$el.querySelectorAll('.project-box')
      ;[].forEach.call(projects, (project) => {
        const tl2 = new TimelineMax()
        tl2
          .from(project.querySelector('.image-holder'), 0.75, {width: '0%', ease: Quint.easeInOut, force3D: true}, 'a')
          .fromTo(project.querySelector('.decoration-gabarit'), 0.3, {borderWidth: 0}, {borderWidth: 10, ease: Sine.easeOut, force3D: true, clearProps: 'all'}, 'b')
          .from(project.querySelectorAll('.date-caption'), 0.3, {alpha: 0, force3D: true, clearProps: 'all'}, 'b')
          .staggerFrom(project.querySelectorAll('p, h3'), 0.3, {x: -30, alpha: 0, force3D: true, clearProps: 'all'}, 0.15, 'b')
        new ScrollMagic.Scene({triggerElement: project, duration: 0})
          // .addIndicators()
          .setTween(tl2)
          .addTo(this.controller)
      })

      // const tl3 = new TimelineMax()
      // tl3
      //   .staggerFrom(this.$el.querySelectorAll('.interest-holder'), 0.3, {alpha: 0, force3D: true}, 0.15)
      // new ScrollMagic.Scene({triggerElement: this.$el.querySelector('.interest-holder'), duration: 0, triggerHook: 0.75, reverse: false})
      //   // .addIndicators()
      //   .setTween(tl3)
      //   .addTo(this.controller)

      let trinkets = this.$el.querySelectorAll('.trinket-link')
      ;[].forEach.call(trinkets, (trinket) => {
        let doodle = new Doodle()
        let list = trinket.querySelectorAll('path, line, circle')
        let trinketDoodle = doodle.normalizeGroup(list)
        let tl2 = new TimelineMax()
        let staggerTime = 2.25 / list.length
        tl2
          .staggerTo(trinketDoodle, 0.6, {end: 1, ease: Sine.easeOut}, staggerTime, 'b')
        new ScrollMagic.Scene({triggerElement: trinket, duration: 0, triggerHook: 0.75})
          // .addIndicators()
          .setTween(tl2)
          .addTo(this.controller)
      })

      const $h2 = this.$el.querySelector('.sketches h2')
      const tl3 = new TimelineMax()
      tl3
        .from($h2, 0.3, {y: '125%', force3D: true, ease: Linear.easeNone})
      new ScrollMagic.Scene({triggerElement: this.$el.querySelector('.sketches'), duration: '100%', triggerHook: 1})
        // .addIndicators()
        .setTween(tl3)
        .addTo(this.controller)
        // .on('start', e => {
        //   e.state === 'BEFORE' ? TweenMax.to($h2, 0.3, {alpha: 1e-9}) : TweenMax.to($h2, 0.3, {alpha: 1})
        // })
        // window.addEventListener('resize', () => {
        //   tl3.duration =
        // }, false)
    },
    enterModal (el, done) {
      // this.motion = 'enter'
      TweenMax.set(el.querySelectorAll('.modal-layer-helper'), {x: '-100%'})
      TweenMax.set(el.querySelectorAll('.flowers-container'), {x: '100%'})
      TweenMax.set(el.querySelectorAll('a'), {x: -20, alpha: 0})

      var tl = new TimelineMax({onComplete: done})
      tl
      .to(el.querySelector('.decoration-box'), 1, {x: '0%', force3D: true, ease: Quint.easeInOut, clearProps: 'all'}, 'a')
      .to(el.querySelector('.dummy-layer'), 1, {x: '0%', force3D: true, ease: Quint.easeInOut, clearProps: 'all'}, 'a+=.15')
      .to(el.querySelector('.dummy-layer .flowers-container'), 1, {x: '0%', force3D: true, ease: Quint.easeInOut}, 'a+=.15')
      .staggerTo(el.querySelectorAll('a'), 0.5, {x: 0, alpha: 1, force3D: true, clearProps: 'all'}, 0.15)
    },
    leaveModal (el, done) {
      // this.motion = 'leave'
      var tl = new TimelineMax({onComplete: done})
      tl.staggerTo(el.querySelectorAll('a'), 0.5, {x: 20, alpha: 0, force3D: true}, 0.15)
      .to(el.querySelector('.dummy-layer'), 1, {x: '100%', force3D: true, ease: Quint.easeInOut}, 'a')
      .to(el.querySelector('.dummy-layer .flowers-container'), 1, {x: '-100%', force3D: true, ease: Quint.easeInOut}, 'a')
      .to(el.querySelector('.decoration-box'), 1, {x: '100%', force3D: true, ease: Quint.easeInOut}, 'a+=.15')
    }
  }
}
</script>

<style lang="scss">
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
body
{
	line-height: 1;
	font-family: 'Montserrat', sans-serif;
	overflow-x: hidden;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
 	background-color: #1c1c1c;
  margin: 0;
  padding: 0;
}

.container
{
	max-width: 1180px;
	margin: auto;
  box-sizing: border-box;
  padding-left: 30px;
  padding-right: 30px;
  @media (min-width: 768px) {
    padding-left: 70px;
    padding-right: 70px;
  }
}
h2
{
	font-family: 'Abril Fatface', cursive;
	font-size: 60px;
	padding-top: 1.2em;
	padding-bottom: 1.2em;
  color: #ffffff;
}
</style>



